.Movie{
    background-color:#fff;
    width:40%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align:start;
        align-items:flex-start;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    margin-bottom:50px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding:0 20px;
    -webkit-box-shadow: 0 8px 38px rgba(133,133,133,0.3), 0 5px 12px rgba(133,133,133,0.22);
            box-shadow: 0 8px 38px rgba(133,133,133,0.3), 0 5px 12px rgba(133,133,133,0.22);
}

.Movie__Columns{
    width:30%;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
}

.Movie__Columns:last-child{
    padding:20px 0;
    width:60%;
}

.Movie h1{
    margin:20px 0 10px 0;
    font-size:20px;
    font-weight:600;
    color:#333;
}

.Movie .Movie__Genres{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    margin-bottom:20px;
   
}

.Movie__Genres .Movie__Genre{
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    margin-right:5px;
    margin-bottom:5px;
    font-size:11px;
    color:#999;
    padding:0.1em 0.5em;
    border:1px solid #999;
    border-radius:2em;
}

.Movie__Genres .Movie__Genre:hover{
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    background-color:#444;
    color:#fff;
}

.Movie .Movie__Synopsis{
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    color:rgb(109, 109, 109);
    overflow:hidden;
}

.Movie .Movie__Poster{
    -webkit-transition:all .5s;
    -o-transition:all .5s;
    transition:all .5s;
    max-width:100%;
    position:relative;
    top:-20px;
    -webkit-box-shadow: -10px 19px 38px rgba(83,83,83,0.3), 10px 15px 12px rgba(80,80,80,0.22);
            box-shadow: -10px 19px 38px rgba(83,83,83,0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

.Movie .Movie__Year{
    -webkit-transition:all .5s;
    -o-transition:all .5s;
    transition:all .5s;
    font-size:10px;
    font-weight:bold;
    display:inline-block;
    padding:0.3em 1em;
    background-color:#444;
    border-radius:2em;
    line-height:1;
    color:#fff;
    -webkit-box-shadow:2px 2px 4px rgba(10,10,10,0.3 );
            box-shadow:2px 2px 4px rgba(10,10,10,0.3 );
}

.Movie:hover .Movie__Poster{
    -webkit-transition:all .5s;
    -o-transition:all .5s;
    transition:all .5s;
    -webkit-transform:translateY(-10px);
        -ms-transform:translateY(-10px);
            transform:translateY(-10px);
}
.Movie:hover .Movie__Year{
    background-color:rgb(92, 34, 174);
}
.Movie:hover h1{
    color:rgb(92, 34, 174); 
}
.Movie:hover .Movie__Synopsis{
    color:#555;
}
@media screen and (min-width:320px) and (max-width:667px){
    .Movie{
        width:100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .Movie{
        width:100%;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .Movie__Poster{
        top:0;
        left:0;
        width:100%;
    }
    .Movie__Columns{
        width:100% !important;
    }
} 


